<template>
    <div>
        <div class="container">
            <div class="form-box">
                <el-form ref="form" :model="ArticleInfo" label-width="80px">
                    <el-form-item label="标题">
                        <el-input v-model="ArticleInfo.Title"></el-input>
                    </el-form-item>
                    <el-form-item label="文本框">
                        <div>
                            <tinymce v-model="ArticleInfo.HtmlContentt" :height="300" />
                        </div>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit">表单提交</el-button>
                        <el-button>取消</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>
<script>
    import { request } from '../../utils/http';
    import Tinymce from '../../components/Tinymce'

    export default {
        name: 'OnlyArticle14',
        components: {
            Tinymce
        },
        data() {
            return {
                ArticleInfo:{},
                query: {
                    id:0
                },
                content: '',
                editorOption: {
                    placeholder: 'Hello World'
                }
            };
        },
        methods: {
            onSubmit() {
                request({url: 'filmbll/savearticle', method:'post', data:this.ArticleInfo }).then((res)=>{
                    if (res.code==200){
                        this.$message.success('提交成功！');
                    }
                });
            } ,
            getData(id) {
                this.query.id = id;
                request({ url:'filmbll/getarticle', method:'post',data: this.query }).then((res)=>{
                    if (res.code==200){
                        console.log(res.obj)
                        this.ArticleInfo = res.obj;
                    }
                });
            },
        },
        created() {
            // console.log(this.$route.query);
            this.getData(14);
        },mounted() {
            // console.log(this.$route.query);
        }
    };
</script>

